////
/// This is a poster comment.
/// It will apply annotations to all items from file.
/// @group utils
///
////


///
///
///@name clearfix
///@param {Boolean} $extend[true] - 
/// 

@mixin clearfix($extend:true) {
  @if $extend {
    @extend %clearfix;
  } @else {
    &:before,
    &:after {
      content: "";
      display: table;
    }
    &:after {
      clear: both;
    }
  }
}

%clearfix {
  @include clearfix(false);
}

/// 
///
///@name center-block
///@param {Boolean} $extend[true] - 
@mixin center-block($extend:true) {
  @if $extend {
    @extend %center-block;
  } @else {
    margin-left: auto;
    margin-right: auto;
  }
}

%center-block {
  @include center-block(false);
}

/// 
/// IE8filter
///
///@name opacity
///@param {Number} $opacity[50] - 
@mixin opacity($opacity:50) {
  opacity: $opacity / 100;
  filter: alpha(opacity=$opacity);
}

%opacity {
  @include opacity;
}

%triangle-basic {
  content: "";
  height: 0;
  width: 0;
  overflow: hidden;
}

///
///
///@name triangle
///@param {String} $direction[top] - 
///@param {Measure} $size[10px] - 
///@param {Color} $borderColor[#fff] - 

@mixin triangle($direction:top, $size:10px, $borderColor:#fff ) {
  @extend %triangle-basic;
  @if $direction == top {
    border-bottom: $size solid $borderColor;
    border-left: $size dashed transparent;
    border-right: $size dashed transparent;
  } @else if $direction == right {
    border-left: $size solid $borderColor;
    border-top: $size dashed transparent;
    border-bottom: $size dashed transparent;
  } @else if $direction == bottom {
    border-top: $size solid $borderColor;
    border-left: $size dashed transparent;
    border-right: $size dashed transparent;
  } @else if $direction == left {
    border-right: $size solid $borderColor;
    border-top: $size dashed transparent;
    border-bottom: $size dashed transparent;
  }
}

///
///
///@name ellipsis
///@param {Number} $rows[top] - 
///@param {Measure} $font-size[10px] - 
///@param {Number} $line-height[1.5] - 
@mixin ellipsis($rows:1 , $font-size:14px, $line-height:1.5) {
  @if $rows == 1 {
    display: inline-block;
    vertical-align: middle;
    white-space: nowrap;
    word-break: keep-all;
    overflow: hidden;
    text-overflow: ellipsis;
  } @else {
    display: -webkit-box;
    overflow: hidden;
    white-space: normal !important;
    text-overflow: ellipsis;
    word-wrap: break-word;
    -webkit-line-clamp: $rows;
    -webkit-box-orient: vertical;
    max-height: #{$rows*$line-height}em;
    line-height: $line-height;
    font-size: $font-size;
  }
}

%ellipsis {
  @include ellipsis(1)
}

%ellipsis-multi {
  @include ellipsis(2);
}

///-
///
///@name multi-row
///@param {Number} $rows[top] - 
///@param {Number} $line-height[1.5] - 
@mixin multi-row($rows:2 , $line-height:1.5 ) {
  display: -webkit-box;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: $rows;
  -webkit-box-orient: vertical;
  height: #{$rows*$line-height}em;
  line-height: $line-height;
}

///Retina image
///
///@name img-retina
///@param {String} $file-1x[] - 1URL
///@param {String} $file-2x[] - 2URL
///@param {Measure} $width-1x[] - 1
///@param {Measure} $height-1x[] - 1
@mixin img-retina($file-1x, $file-2x, $width-1x, $height-1x) {
  background-image: url($file-1x);
  @media only screen and (min-resolution: 192dpi), // IE9-11 don't support dppx
  only screen and (min-resolution: 2dppx) { // Standardized
    background-image: url($file-2x);
    background-size: $width-1x $height-1x;
  }
}

//font-size,rempx
@mixin fontSize($font-size:14px) {
  font-size: $font-size;
  font-size: pxToRem($font-size);
}

