///,
///
///@name block-vertical-middle
@mixin block-vertical-middle {
  font-size: 0;
  &-content {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
    text-align: center;
    font-size: $font-default;
  }
  &:after {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    overflow: hidden;
  }
}

%vertical-middle {
  display: inline-block;
  vertical-align: middle;
  &:after {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    overflow: hidden;
  }
}

///  absolute 
///
/// @name absoluteCenter
/// @param {Measure} $width - 
/// @param {Measure} $height - 
@mixin absoluteCenter($width, $height){
  position: absolute;
  left: 50%;
  top: 50%;
  margin: (-$height)/2 0 0 (-$width)/2;
}

///  CSS 
///
///
/// @name selection
@mixin selection {
  @at-root #{&}::selection {
    @content;
  }

  @at-root #{&}::-moz-selection {
    @content;
  }

  @at-root #{&}::-webkit-selection {
    @content;
  }
}

///  URL 
///
///
/// @name text-breakWord
%text-breakWord {
  word-wrap: break-word;
  word-break: break-word;
}
%text_breakWord {
  @extend %text-breakWord;
}

///  CSS 
///
///
/// @name screenResolution
/// @param {Number} $num - 
@mixin screenResolution($num) {
  @media (-webkit-min-device-pixel-ratio: $num),
  (min--moz-device-pixel-ratio: $num),
  (-o-min-device-pixel-ratio: $num),
  (min-device-pixel-ratio: $num),
  (min-resolution: #{$num}dppx),
  (min-resolution: #{$num*96}dpi) {
    @content;
  }
}

///  Pad  CSS  Pad 
///
///
/// @name screenForPad
@mixin screenForPad {
  @media (min-width:$screen_width_pad) {
    @content;
  }
  @media \0screen {
    @content;
  }
}

///  CSS  screenForPad  Phone  Pad 
///
///
/// @name screenForPhone
@mixin screenForPhone {
  @media (max-width:$screen_width_pad) {
    @content;
  }
}

///  IE 10  CSS  IE10 
///
///
/// @name screenForIE10AndLater
@mixin screenForIE10AndLater {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content;
  }
}

///  IE 8 CSS  IE 8 
///
///
/// @name forIE8
@mixin forIE8 {
  @media \0screen {
    @content;
  }
}

///  IE 9 CSS  IE 9 IE 10  
///
///
/// @name forIE9
@mixin forIE9 {
  @media all and (min-width:0\0) and (min-resolution: .001dpcm) {
    @content;
  }
}
