@font-face {
    font-family: 'iconFont';
    src: url('../fonts/iconFont.eot');
    src: url('../fonts/iconFont.eot') format('embedded-opentype'), url('../fonts/iconFont.ttf') format('truetype'), url('../fonts/iconFont.woff') format('woff'), url('../fonts/iconFont.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}
@font-face {font-family: "iconfontNew";
    src: url('../font/iconfontNew.eot?t=1578481686860'); /* IE9 */
    src: url('../font/iconfontNew.eot?t=1578481686860#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2'),
    url('../font/iconfontNew.woff?t=1578481686860') format('woff'),
    url('../font/iconfontNew.ttf?t=1578481686860') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+ */
    url('../font/iconfontNew.svg?t=1578481686860#iconfont') format('svg'); /* iOS 4.1- */
}
[class^="#{$prefix}-icon-"],
[class*="#{$prefix}-icon-"],
[class*="#{$prefixB}-icon-"]{
    font-family: 'iconFont' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: middle;
    display: inline-block;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
    font-size: $icon-size-default;
    transition: all $default-animation;
    &.disabled {
        cursor: $cursor-disabled;
    }
    // color: $icon-color-default;
    // &:hover {
    //     color: $icon-color-hover;
    // }
    // &:active {
    //     color: $icon-color-active;
    // }
    // &.disabled {
    //     color: $text-color-disabled;
    //     cursor: $cursor-disabled;
    // }
}

[class*="#{$prefixB}-icon-"]{
    font-family: "iconfontNew"!important;
}

.#{$prefix}-icon-color-default {
    color: $icon-color-default;
    &:hover {
        color: $icon-color-hover;
    }
    &.disabled {
        color: $text-color-disabled;
    }
}

.#{$prefix}-icon-color-theme {
    color: $theme-color;
    &:hover {
        color: $theme-color-hover;
    }
    &.disabled {
        color: $text-color-disabled;
        cursor: $cursor-disabled;
    }
}

.#{$prefix}-icon-color-white {
    color: $color-white;
    &:hover {
        color: $color-white;
    }
    &.disabled {
        color: $text-color-disabled;
        cursor: $cursor-disabled;
    }
}

